:root {
  --main-color: #000;
  --color-links: #00acdc;
  --bg-color-primary: #fff;
  --shadow-color: #0000000d;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  box-shadow: 0 2px 4px 0 var(--shadow-color);
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: var(--main-color);
  text-transform: uppercase;
  align-items: center;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--color-links);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--color-links);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}
